body {
  font-size: 15px;
  font-weight: 400;
  line-height: 136%;
}

h1 {
  font-family: Montserrat;
  font-size: 64px;
  font-weight: 500;
  line-height: 103%;
}

h2 {
  font-family: Montserrat;
  font-size: 40px;
  font-weight: 600;
  line-height: 135%;
}

h3 {
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 500;
  line-height: 122%;
}

.content {
  font-family: Open sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 156%;
}

.subtitle {
  font-family: Open sans;
  font-size: 16px;
  line-height: 156%;
  font-weight: 600;
  color: #9dc622;
}

.caption {
  font-family: Montserrat;
  font-size: 24px;
  line-height: 104%;
  font-weight: 600;
}

a {
  display: flex;
  text-decoration: none;
}

