.container {
  width: 100%;
  max-width: 1400px;
  margin: auto;
  position: relative;
}
.container-wide {
  width: 100%;
  max-width: 960px;
  margin: auto;
}
.container-medium {
  width: 100%;
  max-width: 760px;
  margin: auto;
}
.container-small {
  width: 100%;
  max-width: 680px;
  margin: auto;
}
@media screen and (max-width: 1199px) {
  .container {
    padding: 0 16px;
  }
}
@media screen and (max-width: 899px) {
  .container {
    padding: 0 16px;
  }
}

ol {
  list-style-type: none;
  list-style-position: inside;
  counter-set: num;
}
ol li {
  display: flex;
  margin-bottom: 32px;
}
ol li:before {
  font-weight: 700;
  display: block;
  width: 21px;
  margin-right: 24px;
  counter-increment: num;
  content: counter(num);
}

.btn-fomalize {
  color: #8cb31a;
  background: #ffffff;
  text-align: center;
  vertical-align: middle;
  padding: 10px 24px;
  border: 2px solid #9dc622;
  text-transform: uppercase;
  border-radius: 4px;
  transition: all 0.5s linear;
}
.btn-fomalize:hover {
  cursor: pointer;
  background: #9dc622;
  color: #ffffff;
}

.btn-order {
  background: linear-gradient(180deg, #b5da46 0%, #8cb31a 100%);
  box-shadow: 0px 2px 5px rgba(100, 141, 169, 0.5), 0px 1px 1px rgba(114, 153, 0, 0.6);
  border-radius: 5px;
  color: #ffffff;
  font-family: Montserrat;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  padding: 19px 35px;
  outline: none;
  border: none;
  text-align: center;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  transition: linear 0.2s background;
}
.btn-order:hover {
  cursor: pointer;
  background: #ffffff;
  color: #8cb31a;
}

.container .header-content {
  width: 1260px;
  margin-top: 20px;
  margin-bottom: 15px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-phone {
  display: flex;
  align-items: baseline;
}

.phone-icon {
  margin-right: 12px;
}
.phone-icon img {
  width: 10px;
  height: 17px;
}

.content {
  color: #202020;
  font-size: 22px;
  line-height: 27px;
}

.header-nav {
  display: flex;
}

.nav-link {
  margin-right: 45px;
  color: #202020;
  font-family: Montserrat;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  position: relative;
}
.nav-link:hover, .nav-link.active {
  color: #202020;
}
.nav-link:hover:after, .nav-link.active:after {
  width: 100%;
}
.nav-link:after {
  content: "";
  width: 0;
  height: 2px;
  background-color: #9dc622;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
  transition: width 0.2s linear;
}

.nav-link .active {
  width: 100%;
  border: 2px;
  background-color: #9dc622;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
}

.menu {
  display: none;
}

@media screen and (max-width: 1199px) {
  .container-header {
    padding: 0 16px;
  }

  .menu {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2000;
    margin-top: -17px;
  }

  .container-header {
    margin-top: 32px;
    margin-left: 20px;
  }
}
.card {
  height: 768px;
  background: url("/src/assets/static/img/bg.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.container .card-content {
  max-width: 1116px;
  width: 100%;
  margin: 0 0 0 100px;
  padding-top: 72px;
}

.card-content {
  display: flex;
}

.left {
  display: flex;
  width: 39%;
  flex-direction: column;
}

.left-title {
  display: flex;
  align-items: center;
}

.card-title {
  margin-right: 20px;
  font-family: Montserrat;
  font-weight: bold;
  font-size: 72px;
  line-height: 88px;
  color: #b5da47;
}

.card-subtitle {
  margin-top: 10px;
  font-family: Montserrat;
  color: #ffffff;
  font-weight: 600;
  font-size: 24px;
  line-height: 25px;
  letter-spacing: 2px;
}

.for-who {
  font-family: Montserrat;
  color: #ffffff;
  font-weight: 500;
  font-size: 32px;
  line-height: 33px;
}

.card-img {
  margin-top: 56px;
  overflow: hidden;
}
.card-img img {
  display: block;
}

.text-under-card {
  margin-top: 48px;
  margin-bottom: 40px;
  font-family: Open Sans;
  color: #ffffff;
  font-weight: 600;
  font-size: 17px;
  line-height: 25px;
  letter-spacing: 0.7px;
}

button.btn-order {
  width: 36%;
}

.right img {
  position: absolute;
  bottom: -56px;
  right: 150px;
}

/* Modal */
.modal {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 1;
  background-color: #ffffff;
  top: 0;
  left: 0;
  z-index: 2000;
}

.modal-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 250px;
  background-color: #ffffff;
  padding: 15px;
  border: 2px solid #202020;
  border-radius: 5px;
  position: relative;
}

.header-content-m {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-phone-m {
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 30px;
}

.phone-icon-m {
  margin-right: 12px;
}
.phone-icon-m img {
  width: 10px;
  height: 17px;
}

.content-m {
  color: #202020;
  font-size: 22px;
  line-height: 27px;
}

.header-nav-m {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a.nav-link-m {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #202020;
  font-family: Montserrat;
}

.nav-link-m {
  margin-right: 0;
  color: #202020;
  font-family: Montserrat;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}
.nav-link-m:hover, .nav-link-m.active {
  color: #202020;
}
.nav-link-m:hover:after, .nav-link-m.active:after {
  width: 100%;
}
.nav-link-m:after {
  content: "";
  width: 0;
  height: 2px;
  background-color: #9dc622;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
  transition: width 0.2s linear;
}

.nav-link-m .active {
  width: 100%;
  border: 2px;
  background-color: #9dc622;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
}

.close {
  position: absolute;
  top: -40px;
  right: -40px;
  z-index: 2000;
}

.modal.show {
  display: flex;
}

